* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
}

body {
  margin: 0;
}

p, ul, li {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo__image {
  width: 100%;
  margin: 100%;
}

.nav {
  unicode-bidi: isolate;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  position: relative;
}

.nav .is-active {
  color: #00acdc;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.main {
  grid-template-rows: 1fr 1fr;
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 487px) {
  .main {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__description {
  color: #616070;
  height: auto;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__title {
  color: #060b35;
  width: 100%;
  height: auto;
  margin: 0 auto;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__rewiews {
  justify-content: space-between;
  margin-top: 16px;
  display: flex;
}

.card__rewiews-numbers {
  color: #060b35;
  align-items: flex-end;
  font-size: 10px;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  flex-direction: row;
  justify-content: space-around;
  width: 96px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 22px;
  line-height: 18px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: bold;
}
